<template>
  <div>
      <div style="margin-bottom: 20px;">
        <cds-button @click="handleSuccessClick">success</cds-button>
        <cds-button @click="handleInfoClick">info</cds-button> 
        <cds-button @click="handleWarningClick">warning</cds-button>
        <cds-button @click="handleErrorClick">error</cds-button>  
      </div>

      <div v-highlight>
        <pre><code>
          &lt;template&gt;
            &lt;div&gt;
              &lt;cds-button @click="handleSuccessClick"&gt;success&lt;/cds-button&gt;
              &lt;cds-button @click="handleInfoClick"&gt;info&lt;/cds-button&gt; 
              &lt;cds-button @click="handleWarningClick"&gt;warning&lt;/cds-button&gt;
              &lt;cds-button @click="handleErrorClick"&gt;error&lt;/cds-button&gt;  
            &lt;/div&gt;
          &lt;/template&gt;

          &lt;script&gt;
            export default {
              data() {
                return {
                }
              },
              methods: {
                handleSuccessClick () {
