                handleInfoClick () {
                  this.$message('这是一条询问消息，会主动消失。')
                },
                handleWarningClick () {
                  this.$message({content:'这是一条警告消息，会主动消失。', type: 'warning', duration: 1000,closable: true})
                },
                handleErrorClick () {
                  this.$message.error({content: '这是一条错误消息，会主动消失。', duration: 2000})
                },
              }
            }
          &lt;/script&gt;
        </code></pre>
      </div>

