      <cds-table :data="messageData">
        <cds-table-column prop="attr" label="属性"></cds-table-column>
        <cds-table-column prop="attrDescription" label="属性描述" width="300"></cds-table-column>
        <cds-table-column prop="attrType" label="类型"></cds-table-column>
        <cds-table-column prop="attrOptions" label="可选值" width="300"></cds-table-column>
        <cds-table-column prop="attrDefault" label="默认值"></cds-table-column>
      </cds-table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        messageData: [{
          attr: 'type',
          attrDescription: '类型',
          attrType: 'String',
          attrOptions: 'info | success | warning | error',
          attrDefault: 'info'
        },{
