          attr: 'duration',
          attrDescription: 'message消息自动消失的时间',
          attrType: 'Number',
          attrOptions: '',
          attrDefault: '3000'
        }],
      }
    },
    methods: {
      handleSuccessClick () {
        this.$message.success('这是一条成功消息，会主动消失。')
